#root {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
    background-color: #F5F5F5;
    /* font-family: 'Gill Sans', 'Gill Sans MT', Calibri, 'Trebuchet MS', sans-serif; */
    font-family: 'Open sans';
    font-family: 'Noto Sans', 'sans-serif';
    display: grid;
    min-height: 100%;
    /* grid-template-rows: 90% auto; */
    /* position: relative;
    min-height: 100%; */
}

