/* -------------------MOBILE-CSS---------------- */

@media only screen and (max-width: 800px) {
    .App ul {
        list-style: none;
        padding: 0;
    }

    .logo {
        height: 60px;
        width: 100px
    }

    .home {
        text-align: center;
        display: grid;
    }

    .info-tile {
        width: 80%;
    }

    .body {
        width: 100%;
        display: block;
    }

    .body-right {
        align-self: center;
    }

    .footer {
        background-color: #035b99;
        color: white;
        display: grid;
        grid-template-rows: 1fr 1fr;
        grid-row-gap: .3fr;
        text-align: center
    }

    .footer-icons {
        width: 100%;
    }

    .footer-bottom {
        text-align: start;
        background-color: #023960;
        display: grid;
    }

    .footer p {
        font-size: 1.1em;

    }

    .footer-bottom {
        font-size: 20px
    }

    .copyright {
        padding-top: 1em;
    }

    .footer-icons-img {
        /* text-align: center; */
        margin: .2em;
        display: grid;
        grid-template-columns: repeat(3, .3fr);
        justify-items: center;
        justify-content: center;
    }

    .labour-indic {
        margin-top: 5em;
        display: grid;
    }

    .resources {
        margin: 2.2em 0;
        /* display: grid;
        grid-template-rows: repeat(auto-fill, minmax(200px, 3fr)); */
    }

    .login-info-side,
    .signup-text {
        display: none;
        visibility: hidden;
    }

    .sign-up {
        text-align: start;

    }

    /* .sign-up .form-login {
        margin-left: 3em;
        margin-bottom: 3em;
    } */

    .sign-up .form-login .inputs {
        width: 80%;
        justify-self: center;
    }

    .sign-up .form-login input {
        width: 90%;
    }

    .tiles {
        display: grid;
        grid-template-columns: repeat(auto-fill, minmax(250px, 1fr));
        /* justify-self: center */
    }

    .resources {
        margin-top: 2.8em;
    }

}

@media screen and (min-width: 801px) {

    .login-info-side,
    .signup-text {
        display: none;
        visibility: hidden;
    }

    .body {
        width: 100%;
        display: grid;
        grid-template-columns: 1fr .4fr;

    }

    .body-left {
        display: grid;
        grid-gap: 2em;
        margin: 0 2em
    }

    .left,
    .right {
        width: 50%;
    }

    .right {
        justify-self: end;
    }

    .footer {
        display: grid;
        grid-template-rows: 1fr 0.2fr;
        grid-row-gap: 0;
        color: white;
        margin-top: 4em;
    }

    .footer-top {
        display: grid;
        background-color: #035b99;
        grid-template-columns: 0.5fr 0.5fr;
        align-items: center;
        padding: 2em 6em;
    }

    .footer-icons {
        justify-self: end;
        display: grid;
    }

    .footer-icons p {
        justify-items: center;
    }

    .footer-icons-img {
        display: grid;
        grid-template-columns: .3fr .3fr .3fr;
    }

    .footer-icons img {
        padding: 0 0.4em;
    }

    .footer-bottom ul {
        background-color: #023960;
        display: grid;
        grid-template-columns: repeat(6, .2fr);
        height: 100%;
        align-content: center;
        margin: 0;
        padding: 0
    }

    .footer-bottom {
        width: 100%;
        height: 100%;
        padding: 0;
        margin: 0
    }

    .footer-bottom li {
        border-right: 0.1px solid white;
    }

    .footer-bottom .copyright {
        border: none;
    }

    .footer-bottom :last-child {
        border: none;
    }

    .tiles {
        display: grid;
        grid-template-columns: repeat(auto-fill, minmax(250px, 1fr));
        /* justify-self: center */
    }

}

@media screen and (min-width:1200px) {
    .tiles {
        display: grid;
        grid-template-columns: 1fr 1fr 1fr 1fr 1fr;
        justify-content: center
    }

    .login-info-side,
    .signup-text {
        display: grid;
        visibility: visible;
    }

    .login {
        display: grid;
        grid-template-columns: 50% 50%;
    }

    .sign-up {
        display: grid;
        grid-template-columns: 50% 50%;
        color: #035b99;
    }

    .signup-text {
        background-image: url(/Patrick875.github.io/static/media/signup-art.98152280.png);
        background-size: cover;
        background-position: center;
    }

    .login-info-side {
        background-image: url(/Patrick875.github.io/static/media/signin-art.9b19f545.png);
        background-size: cover;
        background-position: center;
        background-repeat: no-repeat;
        margin: 3em 0;

    }


}

/* -------------------DESKTOP CSS--------------------- */
.App {
    margin: 0;
    padding: 0;
    text-align: center;
    min-height: 100vh;
    display: grid;
    width: 100vw;
    box-sizing: border-box;
    /* grid-template-rows: 10% 90%; */
}

h3 {
    text-align: center;
    margin: 2em auto;
}

a {
    color: white;
    /* border-bottom: 1px solid white */
    text-decoration: none;
}

a:hover {
    border-bottom: 1.5px solid white;
    color: white;
    transition: hover .2s ease-in-out;
    text-decoration: none;
}

.header_top_logo a:hover {
    border: none
}

ul {
    list-style: none;
}

li {
    border: none
}

.home {
    margin-top: 6em;
    padding: 0;
    border: none;
    box-sizing: border-box;
    height: 100%;
    width: 100%;
    display: grid;
    -webkit-margin-after: 0;
            margin-block-end: 0;
    -webkit-margin-before: 0;
            margin-block-start: 0;
}

.item-1 {
    height: 50vh;
    width: 100%;
    /* height: 100%; */
    background-image: url(/Patrick875.github.io/static/media/backg1.9137fcea.jpg);
    background-repeat: no-repeat;
    background-size: cover;
    color: white;
    display: grid;
}

.item-2 {
    width: 100%;
    height: 50vh;
    /* height: 100%; */
    background-image: url(/Patrick875.github.io/static/media/backg2.f2db2a18.jpg);
    background-repeat: no-repeat;
    background-size: cover;
    color: white;
    display: grid;
}

.item-3 {
    width: 100%;
    height: 50vh;
    /* height: 100%; */
    background-image: url(/Patrick875.github.io/static/media/backg3.33d055bb.jpg);
    background-repeat: no-repeat;
    background-size: cover;
    color: white;
    display: grid;
}

.legend {

    height: 100%;
    background-color: #504f4f8f;

}

.legend h1 {
    margin-top: 3em;
}

.legend p {
    font-size: 20px;
}

.legend button {
    justify-self: center;
    width: 10em;
    height: 4em;
    padding: 1em;
    margin-top: 1em;
    background-color: #035b99;
    color: white;
    border-radius: 6px;
    box-shadow: 1px 1px 20px 0px #000000;
    -webkit-box-shadow: 1px 1px 20px 0px #000000;
    -moz-box-shadow: 1px 1px 20px 0px #000000;
    text-shadow: 1px 1px 20px #000000;
    border: none;
    text-align: center;
    text-decoration: none;
    cursor: pointer;
}


