.labour-indic .accordion-wrapper {
    width: 46%
}


.info-tile {
    width: 80%;
    justify-self: center;
    box-shadow: 0 1px 4px rgba(0, 0, 0, 0.3),
        0 0 40px rgba(0, 0, 0, 0.1) inset;
    margin-top: 1em;
}

.info-tile:hover {
    transform: scale(1.1);
    transition: transform .5s ease-in-out;
}

/* -------HOME BODY------------------ */


.body a {
    color: black
}

.body-right {
    display: grid;
    align-self: center;
    justify-content: center;
    justify-items: center;

}

.body-left p {
    padding: 0 2em;
}

.body {
    margin-top: 2em;
}

/* ------------------ACCORDION CSS----------------- */
.summary {
    display: grid;
    justify-content: center
}

.accordion_section {
    width: 60%;
    margin: 0 auto;
    border-bottom: 2px solid #ccc;
}

.accordion {
    display: grid;
    grid-template-columns: 1fr 1fr;
    text-align: start;
}

.accordion_title_left {
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(120px, 0.5fr));
    /* 240px */
}

.accordion_content {
    overflow: hidden;
    max-height: 0;
    transition: max-height 0.6s ease;
}

.rotate {
    transform: rotate(90deg);
}

.accordion_icon {
    display: grid;
    justify-content: center;
    align-self: center;
}

.accordion_content ul {
    list-style-type: none;
    display: grid;
    grid-template-columns: 1fr 1fr;
    justify-content: space-between;

}

.accordion_content li a {
    padding: 1em;
    color: #0000EE;
    list-style: none;
    text-decoration: underline;
}

.labour-indic-nav {
    width: 100%;
}

.resources {
    margin-bottom: 2.2em;
    /* display: grid;
    grid-template-rows: repeat(auto-fit, minmax(200px, 3fr)); */
    display: grid;
}


.resources a {
    color: black
}

.resources ul {
    align-self: start;
    background-color: white;
    justify-self: center;
    width: 60%;
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(60px, .5fr));
    margin-top: 0;
    align-items: center;
    padding-top: 0;
    box-shadow: 0 4px 10px rgba(0, 0, 0, 0.3),
        0 0 50px rgba(0, 0, 0, 0.1) inset;

}

/* 
.resources li {
    /* border-right: 1.4px solid white; */
/* padding: 0 2em;
} */
.resources h3,
.resources .title-h4 {
    margin-top: 2em;
    /* margin: 2em 0; */
}

.docs,
.e-books,
.case-studies,
.reports,
.publications {

    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(400px, 2fr));
    grid-gap: 3em;
    margin: 0 4em;
}

.docs div,
.e-books div,
.case-studies div,
.reports div,
.publications div {
    display: grid;
    align-items: center;
    justify-items: center;
    height: 16em;
    box-shadow: 0 1px 4px rgba(0, 0, 0, 0.3),
        0 0 40px rgba(0, 0, 0, 0.1) inset;
}

.download-btn,
.view-btn {
    /* margin-top: 2em; */
    justify-self: center;
    width: 50%;
    outline: none;
    text-align: center;
    border: 1.6px solid #ccc;
    padding: 10px 16px;
    cursor: pointer;
    background: transparent;
    transition: all 200ms ease;
    border-radius: 8px;
    -webkit-appearance: none;
       -moz-appearance: none;
            appearance: none;
    font-size: 14px;
    font-weight: 500;
    height: 40px;
    box-sizing: border-box;
    text-align: center;
    color: white;
    margin-bottom: 1em;
}

.view-btn {
    background-color: #99035B;
}

.download-btn {
    background-color: #035b99;
}

.login-info-side {
    /* background-color: red; */
    background-image: url(/Patrick875.github.io/static/media/signin-art.9b19f545.png);
    background-size: cover;
    background-position: center;
    background-repeat: no-repeat;
    margin: 3em 0;

}

.form-login {
    display: grid;
    height: 70%;
    margin: 3em 0;
    color: #035b99;

}

.form-login .inputs {
    display: grid;
    grid-gap: 0;
    width: 100%;
    justify-self: center;
}

.form-login input {
    width: 50%
}

.form-login h2 {
    margin-bottom: 1em;
}

.form-login label {
    margin: 1em 0;
}

.login-inp {
    width: 100%;
    background: transparent;
    border: none;
    border-bottom: 1px solid #000000;
    outline: none;
}

.login-inp:focus {
    outline: none !important;
}

label {
    display: block;
}

.inputs button {
    margin-top: 2em;
    justify-self: center;
    width: 50%;
    outline: none;
    text-align: center;
    border: 1.6px solid #ccc;
    display: inline-block;
    padding: 10px 16px;
    cursor: pointer;
    text-decoration: none;
    background: transparent;
    transition: all 200ms ease;
    border-radius: 8px;
    -webkit-appearance: none;
    -moz-appearance: none;
    appearance: none;
    position: relative;
    font-size: 14px;
    font-weight: 500;
    line-height: 20px;
    height: 40px;
    box-sizing: border-box;
    text-align: center;
}

